; define('core/ef_plugins/calendar',["jquery", "handlebars"], function (jQuery) {
    (function ($, window, document, undefined) {
        // deps: ["jquery, handlebars"]
        var pluginName = 'calendar',
            today = new Date(),
            defaults = {
                tourCode: "",
                initialDate: today.getMonth + 1 + "-" + today.getDate + "-" + today.getYear,
                daySelector: ".calendar-day.valid",
                dayNumberSelector: ".day-number",
                dayDateSelector: ".day-date",
                monthDropdownSelector: ".calendar-month",
                yearDropdownSelector: ".calendar-year",
                calendarYearsSelector: "#calendarYears",
                calendarMonthsSelector: "#calendarMonths",
                calendarDayNamesSelector: "#calendarDayNames",
                calendarDaysSelector: "#calendarDays",
                noDatesForMonthSelector: "#noDates",
                extensionSelector: ".extension-checkbox:checked",
                excursionSelector: ".excursion-checkbox:checked",
                noDaysErrorClass: ".calendar-no-days",
                invalidMonthErrorClass: "calendar-invalid-month",
                isPricing: false, // toggles price calculation
                checkOptionals: false,
                showPricingHover: false // show price on mouse hover

            };

        function Calendar(element, options) {
            // the 'element' should be the text box we want the calendar to populate
            this.element = element;
            this.$element = $(element);

            this.options = $.extend({}, defaults, options);

            this._defaults = defaults;
            this._name = pluginName;

            this.init();
        }

        Calendar.prototype = {
            init: function () {
                var plugin = this;

                // going to need something to hold all the calendar data
                this.dateJson = {};

                // initialize some element selectors
                this.$calendarYears = $(this.options.calendarYearsSelector);
                this.$calendarMonths = $(this.options.calendarMonthsSelector);
                this.$calendarDayNames = $(this.options.calendarDayNamesSelector);
                this.$calendarDays = $(this.options.calendarDaysSelector);
                this.$validDays = $();
                this.$month = $();
                this.$year = $();
                this.$selectedDay = $();

                // set the date variables
                if (this.options.initialDate.indexOf("/") !== -1)
                    this.options.initialDate = this.options.initialDate.split('/').join('-');
                this.selectedDate = this.options.initialDate;
                var currentDate = this.selectedDate.split("-");
                this.currentMonth = currentDate[0];
                this.currentYear = currentDate[2];
                this.daysInMonth = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];

                // ajax call to get the json for the starting month
                this._ajaxJsonRequest(plugin.currentMonth, plugin.currentYear, "init");
            },

            // ============================
            //  AJAX JSON REQUEST 
            // ============================
            _ajaxJsonRequest: function (month, year, successSwitch) {
                var plugin = this;
                var contentUrl, $result, callback, source, plugin;

                contentUrl = plugin._buildAjaxUrl(month, year);

                $.ajax({
                    type: "GET",
                    url: contentUrl,
                    dataType: "json",
                    success: function (data) {
                        plugin.dateJson = data;
                        if (successSwitch === "year") {
                            plugin._updateYear();
                        } else if (successSwitch === "month") {
                            plugin._updateMonth();
                        }
                            // assume it's init
                        else {
                            plugin._startup();
                        }
                    },
                    error: function (request, status, errorThrown) {

                    }
                });
            },

            // ============================
            //  AJAX JSON REQUEST 
            // ============================
            _buildAjaxUrl: function (month, year) {
                var plugin = this,
                    $extensions,
                    $excursions;

                // base ajax route plus the selected month/year (don't care about day, always use 1 to avoid problems)
                var ajaxUrl = plugin.$element.data('calendar-ajax-load-url') + '/' + month + '-' + 1 + '-' + year;


                // sometimes we want to take optionals into account, because they modify validity range
                if (plugin.options.checkOptionals == true) {

                    // find selected extension codes
                    var extensionCodesString = "";
                    $extensions = $(plugin.options.extensionSelector);
                    if ($extensions.length > 0) {
                        var extensionCodes = [];
                        $(plugin.options.extensionSelector).each(function (index) {
                            if ($(this).val() != null) {
                                extensionCodes.push($(this).val());
                            }
                        });
                        if (extensionCodes.length > 0) {
                            extensionCodesString = extensionCodes.join("|");
                        }
                    }

                    // find selected excursion codes
                    var excursionCodesString = "";
                    $excursions = $(plugin.options.excursionSelector);
                    if ($excursions.length > 0) {
                        var excursionCodes = [];
                        $(plugin.options.excursionSelector).each(function () {
                            if ($(this).val() != null) {
                                excursionCodes.push($(this).val());
                            }
                        });
                        if (excursionCodes.length > 0) {
                            excursionCodesString = excursionCodes.join("|");
                        }
                    }

                    // append extensions/excursions to url as query string
                    var queryString;
                    if (extensionCodesString != "" || excursionCodesString != "") {
                        queryString = "?extensions=" + extensionCodesString + "&excursions=" + excursionCodesString;
                        ajaxUrl += "/" + queryString;
                    }
                }

                return ajaxUrl;
            },

            // ============================
            //  UPDATE METHODS 
            // ============================
            // use the ajax json response to load everything for the first time
            _startup: function () {
                var plugin = this;

                // load the calendar controls
                plugin._displayYearButtons();
                plugin._displayMonthDropdown();

                // load the days in the current month
                plugin._displayDayNames();
                plugin._displayDays();

                // set control and calendar selections
                plugin._selectYear(plugin.currentYear);
                plugin._selectMonth(plugin.currentMonth);
            },
            // use the ajax json response to update the calendar after year is changed
            _updateYear: function (year) {
                var plugin = this;

                // update the month dropdown for the new year
                plugin._displayMonthDropdown();
                plugin._selectMonth(plugin.currentMonth);

                // update the days
                plugin._displayDays();
            },
            // use the ajax json response to update the calendar after month is changed
            _updateMonth: function (year) {
                var plugin = this;

                // update the days
                plugin._displayDays();
            },

            // ============================
            //  DISPLAY METHODS 
            // ============================
            // select the available years and disply buttons for them
            _displayYearButtons: function () {
                var plugin = this;
                var yearButtonsSource = $("#yearButtonsTemplate").html();
                var yearButtonsTemplate = Handlebars.compile(yearButtonsSource);
                var yearButtonsHTML = yearButtonsTemplate(plugin.dateJson);
                this.$calendarYears[0].innerHTML = yearButtonsHTML;
                plugin.$year = $(this.options.yearDropdownSelector);
                plugin._yearClickListen();
            },

            // select the available months and disply a dropdown for them 
            _displayMonthDropdown: function () {
                var plugin = this;
                var monthDropdownSource = $("#monthDropdownTemplate").html();
                var monthDropdownTemplate = Handlebars.compile(monthDropdownSource);
                var monthDropdownHTML;
                if (!plugin._isNullOrEmpty(plugin.dateJson["years"][plugin.currentYear])) {

                    var monthsObj = { "months": [] };
                    for (i = 1; i <= 12; i++) {
                        if (!plugin._isNullOrEmpty(plugin.dateJson["years"][plugin.currentYear]["months"][i])) {
                            monthsObj["months"].push({ "monthNum": i, "monthName": plugin.dateJson["monthNames"][i - 1], "class": "enabled-month", "disabled": "" });
                        } else {
                            monthsObj["months"].push({ "monthNum": i, "monthName": plugin.dateJson["monthNames"][i - 1], "class": "disabled-month", "disabled": "disabled" });
                        }
                    }
                    monthDropdownHTML = monthDropdownTemplate(monthsObj);
                    plugin.$calendarMonths[0].innerHTML = monthDropdownHTML;
                    plugin.$month = $(this.options.monthDropdownSelector);
                    plugin._monthClickListen();
                }
            },

            // gather localized day names from JSON and display in first calendar row
            _displayDayNames: function () {
                var plugin = this;
                var dayNamesSource = $("#dayNamesTemplate").html();
                var dayNamesTemplate = Handlebars.compile(dayNamesSource);
                var daysObj = { "days": [] };
                $.each(plugin.dateJson["dayNames"], function (key, value) {
                    daysObj["days"].push({ "dayName": value.toLowerCase() });
                });
                var dayNamesHTML = dayNamesTemplate(daysObj);
                this.$calendarDayNames[0].innerHTML = dayNamesHTML;
            },

            // select one calendar month, including leading and trailing days, and display it
            _displayDays: function () {
                var plugin = this;

                // hide any previous error message
                $(plugin.options.noDatesForMonthSelector).hide();

                var currentDaysNum = plugin._calcDaysInMonth(plugin.currentMonth, plugin.currentYear);

                // LEADING DAYS
                var leadingDaysNum = new Date(plugin.currentYear, plugin.currentMonth - 1, 1).getDay(); // returns 0-6
                var previousMonth;
                var previousMonthYear = plugin.currentYear;
                if (plugin.currentMonth > 1) {
                    previousMonth = plugin.currentMonth - 1;
                } else {
                    previousMonth = 12;
                    previousMonthYear--;
                }
                var previousMonthLength = plugin._calcDaysInMonth(previousMonth, previousMonthYear);
                var leadingDaysObject = { "leadingDays": {} };
                var dayNum;
                for (i = leadingDaysNum - 1; i >= 0; i--) {
                    dayNum = previousMonthLength - i;
                    leadingDaysObject["leadingDays"][dayNum] = { "dayNum": dayNum, "date": previousMonth + "/" + dayNum + "/" + previousMonthYear };
                }

                // TRAILING DAYS
                var trailingDaysNum;
                var leadingPlusCurrent = currentDaysNum + leadingDaysNum;
                if (leadingPlusCurrent <= 28) {
                    trailingDaysNum = 28 - leadingPlusCurrent;
                } else if (leadingPlusCurrent <= 35) {
                    trailingDaysNum = 35 - leadingPlusCurrent;
                } else {
                    trailingDaysNum = 42 - leadingPlusCurrent;
                }
                var nextMonth;
                var nextMonthYear = plugin.currentYear;
                if (plugin.currentMonth < 12) {
                    nextMonth = plugin.currentMonth + 1;
                } else {
                    nextMonth = 1;
                    nextMonthYear++;
                }
                var trailingDaysObject = { "trailingDays": {} };
                for (i = 1; i <= trailingDaysNum; i++) {
                    trailingDaysObject["trailingDays"][i] = { "dayNum": i, "date": nextMonth + "/" + i + "/" + nextMonthYear };
                }

                // CREATE CALENDAR MONTH JSON
                // check for the current month - there's no reason it wouldn't be there at this point
                // but in case it's not, just return a month of invalid days
                var calendarMonthObject = { "days": {} };
                if (!plugin._isNullOrEmpty(plugin.dateJson["years"][plugin.currentYear]) &&
                        !plugin._isNullOrEmpty(plugin.dateJson["years"][plugin.currentYear]["months"][plugin.currentMonth]) &&
                        !plugin._isNullOrEmpty(plugin.dateJson["years"][plugin.currentYear]["months"][plugin.currentMonth]["days"])
                ) {
                    calendarMonthObject["days"] = plugin.dateJson["years"][plugin.currentYear]["months"][plugin.currentMonth]["days"];
                } else {
                    for (i = 1; i <= currentDaysNum; i++) {
                        calendarMonthObject["days"][i] = { "dayNum": i, "date": plugin.currentMonth + "/" + i + "/" + plugin.currentYear, "validity": "invalid" };
                    }
                    // inform the user that there will be no valid dates this month
                    $noDates = $(plugin.options.noDatesForMonthSelector);
                    $noDates.removeClass("calendar-no-days");
                    $noDates.addClass("calendar-invalid-month");
                    $noDates.show();
                    $(plugin.options.noDatesForMonthSelector).show();
                }
                calendarMonthObject["leadingDays"] = leadingDaysObject["leadingDays"];
                calendarMonthObject["trailingDays"] = trailingDaysObject["trailingDays"];

                // DISPLAY WITH HANDLEBARS
                // choose the proper template
                var daysSource;
                if (plugin.options.isPricing == true) {
                    daysSource = $("#validityAndPricingDaysTemplate").html();
                } else {
                    daysSource = $("#validityDaysTemplate").html();
                }
                // compile
                var daysTemplate = Handlebars.compile(daysSource);
                var daysHTML = daysTemplate(calendarMonthObject);
                plugin.$calendarDays[0].innerHTML = daysHTML;


                // If your currently selected date is in this month, make sure it's selected
                // (if you select a date, go to a different month, and then come back for instance)
                // (also happens on first calendar load)
                plugin.$validDays = $(plugin.options.daySelector);
                var selectedDateParts = plugin.selectedDate.split("-");
                if (plugin.currentYear == selectedDateParts[2] && plugin.currentMonth == selectedDateParts[0]) {
                    plugin._selectDay(selectedDateParts[1]);
                }

                // update the day listener

                plugin._dayClickListen();
            },

            // ============================
            //  SELECT METHODS
            // ============================
            _selectYear: function (year) {
                var plugin = this;

                plugin.$year.val(year);
            },
            _selectMonth: function (month) {
                var plugin = this;

                plugin.$month.val(month);
            },
            _selectDay: function (day) {
                var plugin = this;

                plugin.$validDays.each(function () {
                    var dayNumber = $(this).find(plugin.options.dayNumberSelector);
                    if (dayNumber.text() == day) {
                        plugin.$selectedDay = $(this);
                        plugin.$selectedDay.addClass("selected-day");
                    }
                });
            },

            // ============================
            //  CLICK LISTENERS
            // ============================
            // setup listeners for day clicks
            _dayClickListen: function () {
                var plugin = this;

                plugin.$calendarDays.on('click', plugin.options.daySelector, function (event) {
                    plugin.$selectedDay.removeClass("selected-day");
                    plugin.$selectedDay = $(this);
                    plugin.$selectedDay.addClass("selected-day");
                    var newDate = plugin.$selectedDay.find(plugin.options.dayDateSelector).text().split("/");
                    plugin.selectedDate = newDate[0] + "-" + newDate[1] + "-" + newDate[2];
                    plugin.$element.trigger("readyToApply");
                });
            },
            // setup listeners for day clicks
            _monthClickListen: function () {
                var plugin = this;

                plugin.$month.on('change', function (event) {
                    plugin.currentMonth = plugin.$month.val();
                    if (!plugin._isNullOrEmpty(plugin.dateJson["years"][plugin.currentYear]["months"][plugin.currentMonth])) {
                        plugin._ajaxJsonRequest(plugin.currentMonth, plugin.currentYear, "month");
                    } else {
                        if (plugin.currentMonth != "" && plugin.currentMonth != null) {
                            plugin._displayDays();
                        } else {
                            // the month is null so wipe the days and show an error
                            plugin.$calendarDays[0].innerHTML = "";
                            $noDates = $(plugin.options.noDatesForMonthSelector);
                            $noDates.addClass("calendar-no-days");
                            $noDates.removeClass("calendar-invalid-month");
                            $noDates.show();
                        }
                    }
                });
            },
            // setup listeners for day clicks
            _yearClickListen: function () {
                var plugin = this;

                plugin.$year.on('change', function (event) {
                    plugin.currentYear = plugin.$year.val();

                    // if the currently selected month does not exist in the newly selected year, find one that does
                    if (plugin._isNullOrEmpty(plugin.dateJson["years"][plugin.currentYear]["months"][plugin.currentMonth])) {
                        for (i = 1; i <= 12; i++) {
                            if (!plugin._isNullOrEmpty(plugin.dateJson["years"][plugin.currentYear]["months"][i])) {
                                plugin.currentMonth = i;
                                break;
                            }
                        }
                    }

                    plugin._ajaxJsonRequest(plugin.currentMonth, plugin.currentYear, "year");

                });
            },

            // ============================
            //  HELPER FUNCTIONS
            // ============================
            _isNullOrEmpty: function (property) {
                try {
                    // also fail if exists, but empty
                    if (property != null && property != "" && property != "undefined" && !$.isEmptyObject(property)) {
                        return false;
                    }
                    return true;
                } catch (err) {
                    return true;
                }
            },
            _calcDaysInMonth: function (month, year) {
                var plugin = this;

                //compensate for JS counting months from zero
                month--;

                var monthLength;
                if (month == 1) {
                    if ((year % 4 == 0 && year % 100 != 0) || year % 400 == 0) {
                        monthLength = 29;
                    } else {
                        monthLength = 28;
                    }
                } else {
                    monthLength = plugin.daysInMonth[month];
                }
                return monthLength;
            }
        };

        // A really lightweight plugin wrapper around the constructor,
        // preventing against multiple instantiations
        $.fn[pluginName] = function (options) {
            return this.each(function () {
                if (!$.data(this, 'plugin_' + pluginName)) {
                    $.data(this, 'plugin_' + pluginName,
                        new Calendar(this, options));
                }
            });
        }

    })(jQuery, window, document);
});
